:root {
  --color-black: #222;
  --color-black0: #000;
  --color-gray: #6f6e6e;
  --color-white: #fff;
  --color-whitef8: #f8f8f8;
  --color-red: #ef5870;
  --color-pink: #f16d7f;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.025em;
  color: var(--color-black);
  background-color: var(--color-white);
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
@media (max-width: 768px) {
  .grid {
    grid-template-columns: repeat(8, 1fr);
  }
}
@media (max-width: 540px) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.svg-icon {
  fill: #e8e8e8;
  transition: transform 0.3s linear;
}
.svg-icon:hover {
  transform: scale(1.2);
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

.product {
  margin-bottom: 128px;
}
@media (max-width: 540px) {
  .product {
    margin-bottom: 64px;
  }
}
.product-items {
  gap: 30px;
}
.product-item {
  margin: 0;
  position: relative;
  grid-column: 4 span;
}
.product-item:hover {
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.15);
}
.product-item__photo {
  position: relative;
  width: 100%;
  height: 420px;
  overflow: hidden;
}
@media (max-width: 540px) {
  .product-item__photo {
    height: 418px;
  }
}
.product-item__photo_image {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  transform: translate3d(-50%, -50%, 0);
  z-index: 1;
}
.product-item__hover {
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgba(58, 56, 56, 0.86);
  cursor: pointer;
}
.product-item__hover-dorder {
  padding: 5px;
  display: flex;
  align-items: center;
  background-color: transparent;
  border: 1px solid var(--color-white);
}
.product-item__hover-logo {
  margin-right: 7px;
  width: 23px;
  height: 22px;
}
.product-item__hover-text {
  padding: 12px 0;
  font-size: 13px;
  font-weight: 700;
  color: var(--color-white);
}
.product-item__text {
  padding: 20px 0 20px 15px;
  background-color: var(--color-whitef8);
  cursor: pointer;
}
.product-item__text-title {
  margin-bottom: 15px;
  color: var(--color-black);
  text-transform: uppercase;
}
.product-item__text-description {
  margin-bottom: 18px;
  font-weight: 300;
  line-height: 17px;
  color: #5d5d5d;
}
.product-item__text-price {
  font-size: 16px;
  text-transform: uppercase;
  color: var(--color-pink);
}
@media (max-width: 768px) {
  .product-item_last {
    display: none;
  }
}

.product-item:hover .product-item__photo_image {
  z-index: -1;
}

header {
  position: relative;
  width: 100%;
  z-index: 10;
  background-color: var(--color-black);
}

.header-icons {
  padding: 15px 25px 15px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
@media (max-width: 540px) {
  .header-icons {
    padding-right: 15px;
  }
}
.header-icons__item {
  display: flex;
  align-items: center;
}
.header-icons__logo {
  margin-right: 40px;
}
.header-icons__svg-menu {
  margin-right: 34px;
}
@media (max-width: 540px) {
  .header-icons__svg-menu {
    margin-right: 0;
  }
}
.header-icons__svg-forma {
  margin-right: 33px;
}
.header-icons__cart {
  position: relative;
  display: inline-block;
}

.search-form {
  position: relative;
}
.search-form__field {
  display: inline-block;
  position: absolute;
  padding: 5px;
  top: 0;
  left: 50px;
  width: 250px;
  font-size: 16px;
  font-weight: 700;
  border-radius: 5px;
  cursor: pointer;
}
@media (max-width: 540px) {
  .search-form__field {
    top: 30px;
    left: 0;
  }
}
.search-form__inactive {
  display: none;
}
.search-form__btn {
  background: transparent;
  border: none;
}

.logo__icon {
  transition: transform 0.3s linear;
}
.logo__icon:hover {
  transform: scale(1.2);
}

.header-cart__counter {
  padding: 2px;
  position: absolute;
  display: flex;
  justify-content: center;
  bottom: 20px;
  left: 25px;
  height: 20px;
  width: 20px;
  background-color: var(--color-pink);
  color: var(--color-white);
  border-radius: 50%;
}

@media (max-width: 540px) {
  .header-icons__svg-forma,
.header-icons__cart {
    display: none;
  }
}
.wrapper {
  position: absolute;
  top: 70px;
  right: 0;
  width: 100%;
  height: 758px;
  background-color: rgba(0, 0, 0, 0.5);
  box-shadow: 6px 4px 35px 0px rgba(0, 0, 0, 0.21);
  transform: translate(0, 0);
  transition: all 0.3s linear;
}
.wrapper__inactive {
  transform: translate(-100%, 0);
}

.header-menu {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  width: 232px;
  height: 758px;
  background-color: var(--color-white);
  cursor: pointer;
}
.header-menu__icon {
  position: absolute;
  top: 16px;
  right: 16px;
  transition: transform 0.3s linear;
}
.header-menu__icon:hover {
  transform: scale(1.4);
}
.header-menu__title {
  padding-top: 37px;
  padding-left: 34px;
  margin-bottom: 24px;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--color-black0);
}
.header-menu__section {
  padding-left: 33px;
  margin-bottom: 12px;
  text-transform: uppercase;
  color: var(--color-pink);
  transition: all 0.2s linear;
}
.header-menu__section_item {
  padding-left: 54px;
  margin-bottom: 11px;
  color: var(--color-gray);
  transition: all 0.2s linear;
}
.header-menu__section_item:hover {
  color: var(--color-black0);
  text-decoration: underline;
}
.header-menu__section_item_last {
  margin-bottom: 20px;
}
.header-menu__section:hover {
  color: var(--color-red);
  text-decoration: underline;
}

.presentation {
  background-color: #f1e4e6;
}
.presentation__items {
  background-image: url(../images/presentation/photo.png);
  background-repeat: no-repeat;
  background-position: left;
  background-size: contain;
}
@media (max-width: 768px) {
  .presentation__items {
    background-position: 7%;
  }
}
@media (max-width: 540px) {
  .presentation__items {
    background-image: none;
  }
}
.presentation__item {
  padding: 285px 0 375px;
  grid-column-start: 7;
  grid-column-end: 13;
}
@media (max-width: 900px) {
  .presentation__item {
    padding: 212px 0 263px;
  }
}
@media (max-width: 768px) {
  .presentation__item {
    padding: 138px 0 151px;
    grid-column-start: 5;
    grid-column-end: 9;
  }
}
@media (max-width: 670px) {
  .presentation__item {
    grid-column-start: 4;
  }
}
@media (max-width: 540px) {
  .presentation__item {
    margin: 0 auto;
    padding: 148px 0 149px;
    grid-column-start: 1;
  }
}
.presentation__text {
  padding-left: 26px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-weight: 700;
}
.presentation__text::before {
  content: " ";
  position: absolute;
  left: 0;
  top: 7px;
  bottom: 5px;
  display: block;
  width: 12px;
  background-color: var(--color-pink);
}
.presentation__text_font-size-big {
  font-size: 48px;
  font-weight: 900;
}
@media (max-width: 900px) {
  .presentation__text_font-size-big {
    font-size: 44px;
  }
}
@media (max-width: 540px) {
  .presentation__text_font-size-big {
    font-size: 38px;
  }
}
.presentation__text_font-size {
  font-size: 32px;
}
@media (max-width: 900px) {
  .presentation__text_font-size {
    font-size: 28px;
  }
}
@media (max-width: 768px) {
  .presentation__text_font-size {
    font-size: 24px;
  }
}
@media (max-width: 600px) {
  .presentation__text_font-size {
    font-size: 20px;
  }
}
.presentation__text_font-size-color {
  color: var(--color-pink);
}

.deal {
  padding-top: 63px;
}
.deal-items {
  margin-bottom: 30px;
